function FontTemplate(props) {
  const { className, id } = props.meta;
  return (
    <svg
      xlmns="http://www.w3.org/2000/svg"
      fill="currentColor"
      viewBox="0 0 24 24"
      height="1em"
      width="1em"
      className={className ? className : null}
      id={id ? id : null}
    >
      {props.children}
    </svg>
  );
}

